<template>
  <div class="user-warp">
    <app-header
      title="用户设置"
    ></app-header>
    <div class="user-main">
      <div class="title">完善用户信息</div>
      <mt-field label="手机号码" placeholder="请输入您手机号码" v-model="tel">
        <mt-button type="default" size="small" @click="send">发送</mt-button>
      </mt-field>
      <mt-field label="图形码" v-show="needCode" v-model="vCode">
        <img :src="codeSrc" height="45px" width="100px" @click="refreshVCode ">
      </mt-field>
      <mt-field label="验证码" state="" placeholder="验证码" v-model="code"></mt-field>
      <mt-field label="设置密码" placeholder="请输入您的密码" v-model="password" type="password"></mt-field>
      <mt-field label="确认密码" placeholder="请再次输入您的密码" v-model="passwordx" type="password"></mt-field>
      <mt-button type="primary" plain size="large" style="margin-top: 1rem;" @click="bindTel">提交</mt-button>
    </div>
  </div>
</template>

<script>
import {sendSms, registerCheck} from '../../../api/login'
import {ROOT} from '../../../config/urls'
import AppHeader from '../../../components/header/header.vue'
export default {
  name: 'User-Set',
  data () {
    return {
      tel: '',
      password: '',
      passwordx: '',
      code: '',
      vCode: '',
      codeSrc: '',
      needCode: false
    }
  },
  methods: {
    bindTel () {
      if (!this.tel || !this.password || !this.passwordx) return this.$toast('请输入完整')
      if (this.password !== this.passwordx) return this.$toast('两次密码不一致')
      registerCheck(this.tel, this.code, undefined, this.password, this.passwordx)
        .then(data => {
          if (data.state) {
            this.$toast('绑定成功')
            this.$router.replace('/home')
          } else {
            this.$toast(data.errormsg)
          }
        })
    },
    refreshVCode () {
      let _random = Math.random() * 100
      this.$set(this, 'codeSrc', ROOT + '/vcode?r=' + _random)
    },
    send () {
      if (!this.tel) return this.$toast('请输入手机号码')
      sendSms(this.tel, this.vCode)
        .then(data => {
          if (data.state) {
            this.$toast('发送成功，请注意查收')
          } else {
            this.$toast(data.errormsg)
          }
          if (data.errormsg === '图形码错误') {
            this.$toast('图形码错误')
            this.needCode = true
            this.codeSrc = ROOT + '/vcode'
          }
        })
    }
  },
  components: {
    AppHeader
  }
}
</script>

<style lang="less" scoped>
  .user-wrap{
    height: 100%;
    background-color: #e5e5e5;
    .user-main{
      width: 100%;
      position: absolute;
      padding-top: 50px;
      overflow: hidden;
      background-color: #e5e5e5;
      &:after{
        content: " ";
        height: 5rem;
        display: block;
      }
    }
  }
  .title{
    padding: .6rem;
    color: #999;
  }
</style>



